.navbar {
    grid-column: 1/3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* background-color: black; */
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    padding: 1.2vw 4vw 1.2vw 3vw;
    z-index: 10;
}

.logo-container {
    display: flex;
    align-items: center;
    font-size: 18pt;
    font-weight: 300;
    text-transform: uppercase;
    font-style: italic;
    letter-spacing: 1px;
}

.logo {
    font-size: 25pt;
    margin-right: 10px;
}

.views-and-search, .views, .search {
    display: flex;
}

.views {
    margin-right: 1.5vw;
}

.list-icon, .grid-icon, .search-icon {
    color: white;
    padding: 0 0.2vw;
    transition: 0.2s ease-out;
}

.search-icon {
    margin-left: 0.7vw;
}

.list-icon:hover, .grid-icon:hover, .search-icon:hover {
    cursor: pointer;
    transform: translateX(1px) translateY(1px);
}

.search-bar {
    width: 12vw;
    height: 1.5vw;
    box-sizing: border-box;
    padding: 0.5vw;
    border: none;
    background-color: white;
    transition: 0.15s ease-out;
}

.search-bar:focus {
    outline: none;
    width: 15vw;
}

.search-bar::placeholder {
    font-family: var(--font-roboto), Arial, Helvetica, sans-serif;
    font-weight: 300;
    text-transform: uppercase;
    color: #cccccc;
}

.dropdown {
    position: relative;
}

.dropdown-content {
    width: 15vw; /* 12vw; */
    position: absolute;
    top: 1.5vw;    
    background-color: var(--darkblue);
    color: white;
    font-size: 10pt;
    font-weight: 300;
}

.dropdown-content ul {
    list-style: none;
}

.dropdown-content li {
    padding: 1vw;
    border-bottom: 1px solid var(--lightblue);
}