:root {
  --font-maven-pro: 'Maven Pro';
  --font-roboto: 'Roboto';
  --darkblue: #0e1521;
  --midblue: #151e2b;
  --lightblue: #2c323e;
  --orange: #f74a24;
}

*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  width: 100vw;
  scroll-behavior: smooth;
  font-family: var(--font-roboto), Arial, Helvetica, sans-serif;
  background-color: var(--darkblue);
}

.App {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 1fr 20fr;
}

.loading {
  grid-column: 2/3;
  /* padding: 2vw; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  color: var(--orange);
}