.user-page {
    display: grid;
    grid-template-columns: 25% 75%;
    grid-template-rows: 20% 70% 10%;
    background-color: var(--midblue);
    color: white;
    border-radius: 5px;
    transition: 0.2s ease-out;
    font-weight: 300;
    font-size: 11pt;
    position: relative;
}

.user-page > div {
    display: flex;
}

.header, .footer {
    grid-column: 1/3;    
}
 /* header */
.header {
    border-bottom: 1px solid var(--lightblue);
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.header-name-page {
    font-size: 38pt;
    opacity: 0.05;
    font-style: italic;
    text-transform: uppercase;
    position: absolute;
    top: 1vw;
    left: 2vw;
    /* letter-spacing: 1px; */
    font-weight: 200;
}

.logo-header-1 {
    font-size: 250pt;
    opacity: 0.05;
    overflow: hidden;
    position: absolute;
    top: -2vw;
    right: 1vw;
}

.logo-header-2 {
    font-size: 200pt;
    opacity: 0.05;
    position: absolute;
    /* top: -1vw; */
    right: 19vw;
    overflow: hidden;
}

.close-icon {
    position: absolute;
    top: 1vw;
    right: 1vw;
    font-size: 10pt;
    padding: 5px;
    border-radius: 5px;
    background-color: var(--orange);    
    transition: 0.2s ease-out;
}

.close-icon:hover {
    border-radius: 10px;
    cursor: pointer;
}

/* user */
.user {
    justify-content: center;
}

.avatar-page {
    position: absolute;
    top: 5vw;
    /* left: 3vw; */    
    /* from grid view */
    justify-content: center;
    align-items: center;
    width: 11.5vw;
}

.photo-page {
    width: 100%;
    border-radius: 50%;
    z-index: 1;
    border: 4px solid var(--orange);
    background-color: var(--midblue);
}

.info-page {
    /* border: 1px solid green; */
    margin: 10vw 2.5vw 2.5vw 2.5vw;
    width: 100%;
    padding: 1.5vw;
    background-color: var(--darkblue);
    border-radius: 5px;
}

.name-page {
    align-items: center;
    font-size: 14pt;
    font-weight: 300;
    font-style: italic;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.person-icon-page {
    margin-right: 0.3vw;
    position: relative;
    top: 4px;
    color: var(--orange);
}

.user-link-page {
    /* color: rgba(255, 255, 255, 0.3); */
    color: white;
    position: relative;
    left: 0.3vw;
    text-decoration: none;
    transition: 0.2s ease-out;
    padding: 0.7vw 0;
    display: flex;
    align-items: center;
}

.user-link-page:hover {
    transform: translateX(3px);
    color: rgba(255, 255, 255, 0.4);
}

.link-icon-page {
    font-size: 16pt;
    position: relative;
    /* top: 4px; */
    left: -5px;
    transform: rotate(-45deg);
    color: var(--orange);
}

/* .bio-page {
    margin-top: 0.3vw;
    border-bottom: 1px solid var(--lightblue);
} */

.location-page {
    margin-top: 2vw;
}

.location-icon-page {
    position: relative;
    top: 4px;
    margin-right: 0.3vw;
    color: var(--orange);
}

/* user type */
.user-type-page {
    align-items: center;
    margin-top: 0.5vw;
    padding-top: 0.7vw;
    border-top: 1px solid var(--lightblue);
}

.type-icon-page {
    color: var(--orange);
    margin-right: 0.3vw;
    position: relative;
    top: 0.3vw;
}

/* repos and organisations */
.repos-and-organisation {
    z-index: 1;
    background-color: var(--midblue);
    flex-direction: column;
    padding: 2vw;
}

.repos-title, .organisation-title {
    font-size: 12pt;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-style: italic;
}

.repos-title {
    margin-bottom: 0.7vw;
}

.organisation-title {
    margin-top: 1.5vw;
}

.repos-page {
    width: 95%;
}

.single-repo, .organisation-page {
    padding: 1.2vw 1.5vw;
    background-color: var(--darkblue);
    border-radius: 5px;
    border-left: 4px solid var(--orange);
}

.single-repo {
    margin: 0.5vw 0;
}

.single-repo ul, .single-org ul {
    list-style: none;
}

.single-repo li {
    display: inline-block;
    width: 9vw;    
    border-left: 1px solid var(--lightblue);
}

.single-repo ul > :nth-child(1) {
    text-transform: uppercase;
    width: 17vw;
    letter-spacing: 1px;
    border-left: none;
}

.single-repo ul > :nth-child(2) {
    width: 12vw;
    padding-left: 20px;
    position: relative;
}

.single-repo b {
    color: var(--orange);
    font-weight: 400;
    margin-left: 5px;
}

.arrow-icon-page {
    font-size: 12pt;
    position: relative;
    top: 2px;    
    margin: 0 10px 0 20px;
    color: var(--orange);
}

.repo-link-page {
    text-decoration: none;
    color: white;
    transition: 0.2s ease-out;
    display: inline-block;
}

.repo-link-page:hover {
    transform: translateX(7px);
    color: rgba(255, 255, 255, 0.4);
}

/* organisation */
.organisation-page {
    width: 95%;    
    margin: 1vw 0;
}

.single-org ul {
    display: flex;
    align-items: center;
}

.single-org li {
    display: inline-block;
    padding: 0.2vw;
}

.single-org ul > :nth-child(1) {    
    width: 2vw;
}

.org-avatar {
    width: 80%;
    border-radius: 50%;
    filter: grayscale(1);
}

.org-link {
    text-decoration: none;
    color: white;
}

.no-orgs {
    font-style: italic;
    opacity: 0.2;
}

/* footer */
.footer {
    border-top: 1px solid var(--lightblue);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    background: linear-gradient(120deg, var(--midblue) 70%, var(--lightblue) 30%);
}